<template>
  <div>
    <h3 style="float: left; margin-left: 30px">指纹识别</h3>

    <a-divider/>

    <div style="float: left; margin-left: 60px; text-align: left" :style="'width: ' +(fullWidth*0.6)+ 'px;'">
      <!--          <a-tag style="margin-left: -50px" color="red">配置信息</a-tag>-->
      <a-form-model
          ref="fingerRef"
          :model="finger_form"
          :rules="finger_rules" :layout="layout">

        <a-form-model-item ref="values" prop="values" style="">
          域名 : <a-input  v-model="finger_form.values" @blur="()=> {$refs.values.onFieldBlur();}"
                   placeholder="https://wordpress.org"
                        :style="'width: ' +(fullWidth-850)+ 'px; width: 80%'"
          />
        </a-form-model-item>
      </a-form-model>


    </div>


    <div style="float: right; margin-right: 60px; margin-top: 3px">
      <a-button v-if="finger_status==='识别中'" disabled style="margin-left: 0px; width: 100px"><a-icon type="loading"/>识别中</a-button>
      <a-button v-else type="danger" @click="finger_click" style="margin-left: 0px; width: 100px">开始识别</a-button>
    </div>

    <div style="margin-top: 120px">

      <a-table :columns="finger_columns" :data-source="finger_data" @change="onChange"
               :style="'margin-inline: auto; word-break: break-all; margin-left: 20px; margin-right: 20px'" :pagination="false" :key="1">
        <span slot="result" slot-scope="text, record">
                    <a-tag v-if="record.result.length === 0">无</a-tag>

                    <a-tag v-else color="green" v-for="i in record.result" :key="i">{{ i }}</a-tag>
                </span>
      </a-table>

    </div>

  </div>
</template>

<script>
const finger_columns = [
  {title: '域名', dataIndex: 'domain', key: "domain", width: 200},
  {title: '结果', dataIndex: 'result', key: "result", scopedSlots: {customRender: 'result'}}
]

const finger_data = []

for (let i = 0; i < 0; ++i) {
  finger_data.push({
    key: i,
    domain: "",
    result: ""
  })
}

export default {
  name: 'Finger',
  // components: {ShellList},
  data() {
    // 判断是否输入的是正确的域名
    let isTrueUrl = (rule, value, callback) => {
      let reg = /(https?|ftp|file):\/\/[-A-Za-z0-9+&@#/%?=~_|!:,.;]+[-A-Za-z0-9+&@#/%=~_|]/
      if (!reg.test(value)) {
        callback(new Error('请输入正确的链接'))
      } else {
        callback()
      }
    }
    return {
      fullHeight : document.documentElement.clientHeight,
      fullWidth : document.documentElement.clientWidth,
      finger_columns,
      finger_data,
      layout: 'horizontal',
      // 指纹识别按钮的状态
      finger_btn_status: false,
      // 指纹识别的按钮文字
      finger_status: "开始识别",
      display_optimization : "",

      finger_form: {
        values: ""
      },
      finger_rules: {
        values: [
          {require: true, message: '填写内容不能为空', trigger: 'blur'},
          {validator: isTrueUrl, trigger: 'blur'},
        ],
      },
    }
  },

  mounted() {
  },

  methods: {
    // 识别中显示优化器
    optimizer() {

      let finger_show = window.setInterval(() => {
        setTimeout(() => {
          // display_optimization+='.'

          if (this.display_optimization.length === 4) {
            this.display_optimization = ""
          }
          else if (this.finger_status === "开始识别") {
            clearInterval(finger_show)
            finger_show = null
          }else {
            this.display_optimization += "."

          }
        }, 0)
      }, 500)


    },
    // 指纹识别
    finger_click(){
      this.$refs.fingerRef.validate(async valid => {
        if (!valid) {
          return;
        }else {
          this.finger_data = []
          this.optimizer()

          this.finger_btn_status = true
          this.finger_status = "识别中"

          const {data: res} = await this.$http.post("/finger", {domain: this.finger_form.values})
          // console.log(res.msg[0].result)
          if (res.status === true) {
            this.finger_btn_status = false
            this.finger_status = "开始识别"
            this.display_optimization = ""
            this.finger_data = res.msg

          }else {
            this.$message.error("识别失败")
          }
        }
      })
    },

    onChange(pagination, filters, sorter) {
      // eslint-disable-next-line no-console
      console.log('params', pagination, filters, sorter)
    },
  },

  created() {

    this.overtimer = window.setInterval(() => {
      setTimeout(() => {
        // console.log(document.documentElement.clientHeight, "finger height")
        this.fullHeight = document.documentElement.clientHeight
        this.fullWidth = document.documentElement.clientWidth
      })

    }, 1000)
    this.$once('hook:beforeDestroy', () => {
      clearInterval(this.overtimer);
      this.overtimer = null;
    })

  },

}
</script>


